import { Component, OnDestroy } from '@angular/core';
import { NbThemeService, NbColorHelper } from '@nebular/theme';

@Component({
  selector: 'ngx-chartjs-bar',
  template: `
    <chart type="bar" [data]="data" [options]="options"></chart>
  `,
})
export class ChartjsBarComponent implements OnDestroy {
  data: any;
  options: any;
  themeSubscription: any;

  constructor(private theme: NbThemeService) {
    this.themeSubscription = this.theme.getJsTheme().subscribe(config => {

      const colors: any = config.variables;
      const chartjs: any = config.variables.chartjs;

      this.data = {
        labels: ['Fev', 'Mar', 'Abr', 'Mai', 'Jun', 'Jul', 'Ago', 'Set', 'Out', 'Nov', 'Dez', 'Jan', 'Fev', 'Mar', 'Abr'],
        datasets: [{
          data: [20, 80, 70, 30, 115, 20, 10, 0, 20, 80, 0, 20, 0, 70, 0],
          label: 'receitas',
          backgroundColor: 'rgba(116, 228, 0, 0.8)',
          borderWidth: 1
        }, {
          data: [30, 70, 80, 20, 50, 30, 12, 50, 10, 20, 25, 30, 20, 22, 10],
          label: 'despesas',
          backgroundColor: 'rgba(249, 82, 82, 0.8)',
        }, {
          data: [20, 30, 15, 40, 100, 80, 80, 20, 25, 85, 50, 50, 30, 50, 50],
          label: 'saldo',
          backgroundColor: 'rgba(6, 182, 255, 0.1)',
          type: 'line'
        }],
      };

      this.options = {
        maintainAspectRatio: false,
        responsive: true,
        legend: {
          labels: {
            fontColor: chartjs.textColor,
          },
        },
        scales: {
          xAxes: [
            {
              barThickness: 25,
              gridLines: {
                display: false,
                color: chartjs.axisLineColor,
              },
              ticks: {
                fontColor: chartjs.textColor,
              },
            },
          ],
          yAxes: [
            {
              gridLines: {
                display: true,
                color: chartjs.axisLineColor,
              },
              ticks: {
                fontColor: chartjs.textColor,
              },
            },
          ],
        },
      };
    });
  }

  ngOnDestroy(): void {
    this.themeSubscription.unsubscribe();
  }
}
